  display: flex;
  margin-top: .1cm;
  align-items: center;
  column-gap: .15cm;
}

.footer-email {
  font-weight: 600;
}

.footer-icon {
  width: .7cm;
  height: auto;
}


.footer-box {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  padding-bottom: .3cm;
  border-bottom: thin solid #c4c4c4;


}

.second-footer {
  display: flex;

  column-gap: 2cm;
}


.company-footer {
  width: 8cm;

}

.footer-logo {
  font-family: Kalam;
  font-size: 30px;
}

.footer-about {
  font-size: 15px;
  color: rgb(48, 48, 48);


}


.footer-lower-txt {
  font-size: 15px;
  color: rgb(48, 48, 48);


}

.lower-footer-icons {
  display: flex;
  align-items: center;
  column-gap: .13cm;
}

.lower-footer-icon {
  width: .6cm;
  height: auto;
  cursor: pointer;
  transition: .2s all;

  &:hover {
    margin-top: -0.07cm;
  }
}

.filter-search-main {
  width: calc(100% - .4cm);
  border-radius: 5px;
  flex: 1;

  border: 1px solid rgb(117, 117, 117);
  transition: .3s;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: .1cm;
  padding: .1cm .2cm;
}

.search-bar-brand {
  width: 100%;


  border: none;

  &:focus {
    outline: none;
  }


}

.sbclose {
  width: .25cm;
  opacity: .7;
  cursor: pointer;
  height: auto;
}

.filter-head-div {
  display: flex;
  align-items: center;
  height: fit-content;
  gap: .2cm;
  justify-content: space-between;
  margin: 0cm 0cm .15cm 0cm;

}

.right-filter-head-div {
  display: flex;
  align-items: center;
  gap: .2cm;
}

.sb {
  width: .35cm;
  height: auto;
  cursor: pointer;
  opacity: .8;
}

.lower-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  row-gap: .3cm;
  padding: .3cm 0cm .5cm 0cm;
}

.footer-menuItem {
  font-size: 15px;
  cursor: pointer;
  color: rgb(48, 48, 48);


}

@media only screen and (max-width : 800px) {
  .second-footer {
    flex-direction: column;
    row-gap: 1cm;
  }
}

@media only screen and (max-width:600px) {

  .footer-reach,
  .footer-head-sub {
    font-size: 16px;
  }



  .company-footer {
    width: 100%;
  }

  .footer-box {
    flex-direction: column;
    row-gap: 1cm;
    // align-items: center;
  }

  .second-footer {
    flex-direction: row;
  }



  .Footer {
    padding: 0cm 5vw;
  }
}

@media only screen and (max-width : 500px) {

  .second-footer {
    column-gap: 1cm;
  }

  .lower-footer-icon {
    width: .45cm;
  }

  .footer-icon {
    width: .5cm;
  }


}


@media only screen and (max-width : 300px) {

  .second-footer {
    flex-direction: column;
    column-gap: .5cm;
    // align-items: flex-start;
  }

}